var div:HTMLDivElement = document.createElement('div')
div.style.border = '1px solid red'
div.style.height = '100px'
div.style.width = '100px'
div.id = 'demo'

document.body.appendChild(div)

var x:boolean = false
var position: [number, number] = [0,0]

div.onmousedown = (e:MouseEvent) => {
  x = true
  position = [e.clientX, e.clientY]
}

document.onmousemove = (e:MouseEvent) => {
  if(x === true){
    var deltaX:number = e.clientX - position[0]
    var deltaY:number = e.clientY - position[1]
    // div.style.top! 后面加感叹号表示 前面的值绝对不会为空
    var top:number = parseInt(div.style.top!) || 0
    var left:number = parseInt(div.style.left!) || 0
    div.style.top = top + deltaY + 'px'
    div.style.left = left + deltaX + 'px'
    position = [e.clientX, e.clientY]
  }
  
}

document.onmouseup = () => {
  x = false
}